import React from 'react';
import Typography from '@components/atoms/typography';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import { styled } from 'linaria/react';
import Image from '@components/atoms/image';
import { CryptoCurrencyWidget } from '@widgets/trading-widget';

const StyledCryptoCurrencyWidget = styled(CryptoCurrencyWidget)`
  height: 400px;
  width: 100%;
`;

const StyledCryptoCurrencyContainer = styled(Container)`
  & > .title2 {
    color: var(--base-title-color);
  }

  ${StyledCryptoCurrencyWidget} {
    margin-top: 20px;
  }
`;

const StyledCryptoCurrencySection = styled(Section)`
  padding-top: 0;

  background-image: var(--base-gradient-color);
  background-position: 100% 100%;
  background-size: 100% 70%;
  background-repeat: no-repeat;
`;

const CryptoCurrencySectionComponent = () => {
  return (
    <StyledCryptoCurrencySection variant='sm'>
      <StyledCryptoCurrencyContainer isFluid>
        <Typography as='h2' className='title2' align='center'>
          Token
        </Typography>
        <StyledCryptoCurrencyWidget />
      </StyledCryptoCurrencyContainer>
    </StyledCryptoCurrencySection>
  );
};

export default CryptoCurrencySectionComponent;
