import React from 'react';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { styled } from 'linaria/react';
import Typography from '@components/atoms/typography';
import { media } from '@styles/media';

const LeftTextWrapper = styled.div`
  width: 100%;

  .title2 {
    margin-top: 12px;
    margin-bottom: 20px;
  }
`;

const RightTextWrapper = styled.div`
  width: 100%;

  .text2 {
    margin-top: 30px;
  }
`;

const InvestSection = styled(Section)`
  background-image: var(--base-gradient-color);
`;

const InvestContainer = styled(Container)`
  align-items: flex-start;

  ${RightTextWrapper} {
    margin-top: 40px;
  }

  ${media.up('xlg')} {
    flex-direction: row;

    ${LeftTextWrapper} {
      margin-right: 100px;
    }

    ${RightTextWrapper} {
      margin-top: 60px;
    }
  }
`;

const InvestPlanSectionComponent = () => {
  return (
    <InvestSection variant='sm'>
      <InvestContainer isFluid>
        <LeftTextWrapper>
          <Typography as='span' className='subtitle2'>
            OUR
          </Typography>
          <Typography as='h2' className='title2'>
            Invest plan's
          </Typography>
          <Typography as='p' className='text2'>
            We enables investors earn 5% - 25% monthly as (income arrives
            weekly), the minimum investment is $1000 USD.
          </Typography>
        </LeftTextWrapper>
        <RightTextWrapper>
          <Typography as='span' className='subtitle2'>
            AI ARBITRAGE REFERRAL PROGRAM
          </Typography>
          <Typography as='p' className='text2'>
            You can also enjoy great benefits by inviting your friends and
            family, business partners, colleagues, etc. to join our affiliate
            program. You will earn 100% of what your referral earns as income.
            In this case if he earns $200USD every month you will earn $200USD
            every month too (direct line).
          </Typography>
        </RightTextWrapper>
      </InvestContainer>
    </InvestSection>
  );
};

export default InvestPlanSectionComponent;
