import React from 'react';
import { styled } from 'linaria/react';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import Image from '@components/atoms/image';
import Button from '@components/atoms/button';
import Typography from '@components/atoms/typography';
import { media } from '@styles/media';

const AboutImage = styled(Image)`
  max-width: 435px;
`;

const StyledAboutInfo = styled.div`
  .title1,
  .text2 {
    color: var(--base-title-color);
  }

  .text2 {
    margin: 40px 0;
  }
`;

const AboutContainer = styled(Container)`
  ${media.up('xlg')} {
    flex-direction: row;
    justify-content: space-between;

    ${AboutImage} {
      flex: 1;
      margin-right: 100px;
    }

    ${StyledAboutInfo} {
      flex: 1;
    }
  }
`;

const AboutSection = styled(Section)`
  /* background-image: var(--base-gradient-color);
  min-height: calc(100vh - 60px); */
`;

const AboutSectionComponent = () => {
  return (
    <AboutSection className='about'>
      <AboutContainer isFluid={true}>
        <AboutImage alt='' src='/images/image.png' />
        <StyledAboutInfo>
          <Typography as='h2' className='title1'>
            You Invest and Let Your Money Work for You
          </Typography>
          <Typography as='p' className='text2'>
            Our world link coin system is built to generate passive income with
            your investment and significantly multiply profits for our
            customers. We do not use other customers capital to pay you, we use
            a powerful artificial intelligence software at the background (ai is
            built into it) it has under gone scrutiny and has been approved for
            its efficiency, stability in performance and ability to give high
            and profitable returns. We are the most trusted platform and a
            global leader in digital asset management in cryptocurrency.
          </Typography>
          <Button variant='primary'>Login</Button>
        </StyledAboutInfo>
      </AboutContainer>
    </AboutSection>
  );
};

export default AboutSectionComponent;
