import React from 'react';
import Container from '@components/atoms/container';
import Section from '@components/atoms/section';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import Typography from '@components/atoms/typography';
import { styled } from 'linaria/react';
import { uppercase } from '@styles/helpers';
import { cx } from 'linaria';
import { PatnersListContainer } from '../partners-list';

const StyledPartnersContainer = styled(Container)`
  text-align: center;

  & > .title2 {
    color: var(--darken-accent-color);
    margin-top: 10px;
    margin-bottom: 20px;
  }

  & > .text2 {
    color: var(--darken-accent-color);
  }

  .team-list {
    margin-top: 30px;
  }

  ${media.up('md')} {
    margin-bottom: 30px;
    margin-top: 20px;

    .team-list {
      margin-top: 40px;
    }
  }

  ${media.up('xlg')} {
    .team-list {
      margin-top: 70px;
    }
  }
`;

const StyledPartnersSection = styled(Section)`
  padding-top: 0;
`;

const PartnersSection = () => {
  return (
    <StyledPartnersSection>
      <StyledPartnersContainer isFluid>
        <Typography as='span' className={cx(uppercase, 'subtitle2')}>
          PARTNERS
        </Typography>
        <Typography as='h2' className='title2'>
          Our Clients
        </Typography>
        <PatnersListContainer className='partners-list' />
      </StyledPartnersContainer>
    </StyledPartnersSection>
  );
};

export default PartnersSection;
