import React from 'react';
import Section from '@components/atoms/section';
import Typography from '@components/atoms/typography';
import Container from '@components/atoms/container';
import { styled } from 'linaria/react';
import { media } from '@styles/media';
import Svg from '@components/atoms/svg';
import { ReactComponent as InvestStepSvg1 } from '@images/invest-step/sign-up-step.svg';
import { ReactComponent as InvestStepSvg2 } from '@images/invest-step/investor-step.svg';
import { ReactComponent as InvestStepSvg3 } from '@images/invest-step/budget-step.svg';

const StyledInvestStepCardImage = styled.div`
  width: 80px;
  margin: 0 auto;
`;

const StyledInvestStepCardContainer = styled.div`
  width: 100%;
  max-width: 300px;

  & > .subtitle2 {
    text-align: center;
    margin-top: 24px;
    color: var(--base-title-color);
    text-transform: uppercase;
  }
`;

const InvestStepCard = ({ image, text }) => {
  return (
    <StyledInvestStepCardContainer>
      <StyledInvestStepCardImage>{image}</StyledInvestStepCardImage>
      <Typography as='p' className='subtitle2'>
        {text}
      </Typography>
    </StyledInvestStepCardContainer>
  );
};

const StyledInvestStepCardListItem = styled.li`
  display: flex;
  justify-content: center;
`;

const StyledInvestStepCardList = styled.ul`
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 50px;

  ${media.up('md')} {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
`;

const InvestStepCardList = () => {
  return (
    <StyledInvestStepCardList>
      <StyledInvestStepCardListItem>
        <InvestStepCard
          image={<Svg svg={<InvestStepSvg1 />} />}
          text='Sign up for an account'
        />
      </StyledInvestStepCardListItem>
      <StyledInvestStepCardListItem>
        <InvestStepCard
          image={<Svg svg={<InvestStepSvg2 />} />}
          text='Choose Investment Plan'
        />
      </StyledInvestStepCardListItem>
      <StyledInvestStepCardListItem>
        <InvestStepCard
          image={<Svg svg={<InvestStepSvg3 />} />}
          text='Pay for the plan'
        />
      </StyledInvestStepCardListItem>
    </StyledInvestStepCardList>
  );
};

const StyledInvestCardListWrapper = styled.div`
  margin-top: 30px;
  width: 100%;

  ${media.up('md')} {
    margin-top: 60px;
  }
`;

const InvestPlatformContainer = styled(Container)`
  color: var(--base-title-color);

  & > .title2 {
    color: inherit;
  }

  & > .text2 {
    color: inherit;
    margin: 20px 0 60px;
  }
`;

const InvestPlatformSection = styled(Section)``;

const InvestPlatform = () => {
  return (
    <InvestPlatformSection>
      <InvestPlatformContainer isFluid>
        <Typography as='h2' className='title2'>
          Getting Started With Your Investment In Few Minutes
        </Typography>
        <Typography as='p' className='text2'>
          Our investment platform is built around intuitive and highly
          accessible user interface. The whole process has been simplified. You
          just need to follow the three simple steps;
        </Typography>
        <StyledInvestCardListWrapper>
          <InvestStepCardList />
        </StyledInvestCardListWrapper>
      </InvestPlatformContainer>
    </InvestPlatformSection>
  );
};

export default InvestPlatform;
