import React from 'react';
import Typography from '@components/atoms/typography';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import { styled } from 'linaria/react';
import Image from '@components/atoms/image';
import Slider from '@components/atoms/slider';
import Hexagon from '@components/atoms/hexagon';
import { cx } from 'linaria';
import { ReactComponent as ArrowLeftSvg } from '@images/arrow-left.svg';
import { ReactComponent as ArrowRightSvg } from '@images/arrow-right.svg';

const StyledHexagonWrapper = styled.div`
  position: relative;

  display: flex;
  justify-content: center;
  margin-bottom: -25px;
`;

const StyledRoadMapContainer = styled.div`
  width: 180px;
  margin: 0 auto;
`;

const StyledIcon = styled.div`
  position: absolute;
  z-index: 1;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 17px;
  height: 17px;

  text-align: center;

  &.text {
    font-size: 1.2rem;

    font-weight: bold;
    color: var(--base-text-color);
  }

  &.svg {
    fill: var(--base-text-color);
  }
`;

const StyledTextWrapper = styled.div`
  padding: 0 16px 16px;
  margin-top: 36px;

  & > .subtitle2 {
    font-weight: bold;
    color: var(--primary-text-color);
  }

  & > .text2 {
    margin-top: 16px;
    font-size: 0.9rem;
  }
`;

const StyledCardBody = styled.div`
  text-align: center;
  margin-top: 3px;
  min-height: 220px;

  background-color: #2275cb;
  border-radius: 8px;

  display: flex;
  flex-direction: column;
  align-items: center;
`;

const RoadMapCard = ({ icon, title, description }) => {
  title = 'Q4 2018';
  description =
    'Beta version of the AI analytical platform, closed testing and finalizition';
  return (
    <StyledRoadMapContainer>
      <StyledHexagonWrapper>
        <StyledIcon className='text'>{icon}</StyledIcon>
        <Hexagon color='#28B446' />
      </StyledHexagonWrapper>
      <StyledCardBody>
        <StyledTextWrapper>
          <Typography as='h3' className='subtitle2'>
            {title}
          </Typography>
          <Typography as='p' className='text2'>
            {description}
          </Typography>
        </StyledTextWrapper>
      </StyledCardBody>
    </StyledRoadMapContainer>
  );
};

const SliderItem = styled.div`
  width: 100%;
  height: 100%;
`;

const StyledSliderWrapper = styled.div`
  width: calc(100% - 100px);
  height: 240px;
`;

const StyledArrow = styled(Button)`
  position: absolute;

  width: 40px;
  height: 40px;

  top: 50%;
  transform: translate(0, -50%);

  background-color: #3b5998;
  border-radius: 50%;

  &.next {
    right: -50px;
  }

  &.prev {
    left: -50px;
  }
`;

function SampleArrow({ onClick, type, children }) {
  return (
    <StyledArrow className={cx(type && type)} onClick={onClick}>
      {children}
    </StyledArrow>
  );
}

const RoadMapSlider = ({ className }) => {
  const settings = {
    infinite: true,
    speed: 300,
    slidesToShow: 5,
    slidesToScroll: 1,
    swipeToSlide: true,
    nextArrow: (
      <SampleArrow type='next'>
        <ArrowRightSvg />
      </SampleArrow>
    ),
    prevArrow: (
      <SampleArrow type='prev'>
        <ArrowLeftSvg />
      </SampleArrow>
    ),
    responsive: [
      {
        breakpoint: 1280,
        settings: {
          slidesToShow: 4,
        },
      },
      {
        breakpoint: 1100,
        settings: {
          slidesToShow: 4,
        },
      },
      {
        breakpoint: 900,
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
        },
      },
      {
        breakpoint: 320,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };

  return (
    <StyledSliderWrapper className={className}>
      <Slider {...settings}>
        <SliderItem>
          <RoadMapCard />
        </SliderItem>
        <SliderItem>
          <RoadMapCard />
        </SliderItem>
        <SliderItem>
          <RoadMapCard />
        </SliderItem>
        <SliderItem>
          <RoadMapCard />
        </SliderItem>
        <SliderItem>
          <RoadMapCard />
        </SliderItem>
        <SliderItem>
          <RoadMapCard />
        </SliderItem>
      </Slider>
    </StyledSliderWrapper>
  );
};

const RoadMapSection = styled(Section)``;

const RoadMapContainer = styled(Container)`
  & > .title2 {
    color: var(--base-title-color);
    margin: 10px 0 20px;
  }

  & > .text2 {
    color: var(--base-title-color);
  }

  .roadmap-slider {
    margin-top: 20px;
  }

  ${media.up('md')} {
    & > .title2 {
      margin-top: 20px 0 30px;
    }

    .roadmap-slider {
      margin-top: 40 px;
    }
  }

  ${media.up('xlg')} {
    & > .text2 {
      max-width: 450px;
    }
  }
`;

const RoadMap = () => {
  return (
    <RoadMapSection>
      <RoadMapContainer isFluid>
        <Typography as='span' align='center' className='subtitle2'>
          Road map
        </Typography>
        <Typography as='h2' align='center' className='title2'>
          World Link Coin Timeline
        </Typography>
        <Typography as='p' align='center' className='text2'>
          World Link Coin is a curated directory of the best cryptocurrency
          resources. We're slowly transforming the website into the best place
          for beginners to learn about cryptocurrencies
        </Typography>
        <RoadMapSlider className='roadmap-slider' />
      </RoadMapContainer>
    </RoadMapSection>
  );
};

export default RoadMap;
