import React from 'react';
import { styled } from 'linaria/react';

import PartnersItem from './partners-item.component';

import { media } from '@styles/media';

const StyledPartnersList = styled.ul`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px 40px;

  ${media.up('md')} {
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px 100px;
  }

  ${media.up('xlg')} {
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 60px;
  }
`;

const StyledPartnersListItem = styled.li``;

const PartnersList = ({ className, data }) => {
  const list = data.map(({ src, id }) => {
    return (
      <StyledPartnersListItem key={id}>
        <PartnersItem src={src} id={id} />
      </StyledPartnersListItem>
    );
  });

  return <StyledPartnersList className={className}>{list}</StyledPartnersList>;
};

export default PartnersList;
