import React from 'react';
import TradingWidget from './trading-widget.component';

const config = {
  width: '100%',
  height: '100%',
  defaultColumn: 'overview',
  screener_type: 'crypto_mkt',
  displayCurrency: 'USD',
  colorTheme: 'dark',
  locale: 'en',
};

export const CryptoCurrencyWidget = (props) => {
  return (
    <TradingWidget
      {...props}
      src='https://s3.tradingview.com/external-embedding/embed-widget-screener.js'
      config={config}
    />
  );
};

export default CryptoCurrencyWidget;
