import React from 'react';
import { TickerTapeWidget } from '@widgets/trading-widget';
import { styled } from 'linaria/react';

const StyledTickerTapeWidgetSection = styled.section`
  min-height: 50px;
  width: 100vw;

  /* width: calc(100vw + 80px); */
  /* overflow: hidden; */
`;

const TickerTapeWidgetSection = () => {
  return (
    <StyledTickerTapeWidgetSection className='rofl'>
      <TickerTapeWidget />
    </StyledTickerTapeWidgetSection>
  );
};

export default TickerTapeWidgetSection;
