import React from 'react';
import Typography from '@components/atoms/typography';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import { styled } from 'linaria/react';
import Image from '@components/atoms/image';
import { uppercase } from '@styles/helpers';
import { cx } from 'linaria';

import { TeamList } from '../team-list';

// const StyledHeader
// const TeamCard = () => {};

const StyledTeamSection = styled(Section)``;
const StyledTeamContainer = styled(Container)`
  text-align: center;

  & > .title2 {
    color: var(--darken-accent-color);
    margin-top: 10px;
    margin-bottom: 20px;
  }

  & > .text2 {
    color: var(--darken-accent-color);
  }

  .team-list {
    margin-top: 30px;
  }

  ${media.up('md')} {
    margin-bottom: 30px;
    margin-top: 20px;

    .team-list {
      margin-top: 40px;
    }
  }

  ${media.up('xlg')} {
    .team-list {
      margin-top: 70px;
    }
  }
`;

const TeamSection = () => {
  return (
    <StyledTeamSection>
      <StyledTeamContainer isFluid>
        <Typography as='span' className={cx(uppercase, 'subtitle2')}>
          OUR CORE TEAM
        </Typography>
        <Typography as='h2' className='title2'>
          Team and consultants
        </Typography>
        <Typography as='p' className='text2'>
          The WorldLinkCoin team is one of the pioneers in the development of AI
          which is based on neural networks in field of crypto finance and
          Arbitrage system. Our team consists of experienced professionals who
          have worked in top crypto firms and have been developing AI
          algorithmic software for many years, with over 20 years of experience
          in the field of AI development. We are committed to delivering cutting
          edge technology that drives results for our customers.
        </Typography>
        <TeamList className='team-list' />
      </StyledTeamContainer>
    </StyledTeamSection>
  );
};

export default TeamSection;
