import React from 'react';
import Typography from '@components/atoms/typography';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import { styled } from 'linaria/react';
import Image from '@components/atoms/image';

const SectionImage = styled(Image)`
  max-width: 477px;
`;

const StyledImageWrapper = styled.div`
  padding: 0 20px;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const JoinButton = styled(Button)`
  margin-top: 30px;
  align-self: flex-start;
`;

const StyledTextWrapper = styled.div`
  & > .title2 {
    margin-bottom: 30px;
  }

  & > .text2 + .text2 {
    margin-top: 16px;
  }
`;

const WorksContainer = styled(Container)`
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 30px;

  ${media.up('xlg')} {
    grid-row-gap: 50px;

    &.col-xlg-2 {
      grid-template-columns: 1fr 1fr;
    }
  }

  ${media.up('xlg')} {
    grid-column-gap: 100px;

    ${StyledTextWrapper} {
      order: -1;
    }
  }
`;

const WorksSection = styled(Section)`
  background-image: var(--base-gradient-color);
`;

const WorksSectionComponent = () => {
  return (
    <WorksSection variant='sm'>
      <WorksContainer isFluid className='col-xlg-2'>
        <StyledImageWrapper>
          <SectionImage src='/images/ai.png' />
        </StyledImageWrapper>
        <StyledTextWrapper>
          <Typography as='h2' className='title2'>
            How it Works
          </Typography>
          <Typography as='p' className='text2'>
            Our platform is the most trusted and most powerful AI investment
            platform, our platform is built on the latest technology and we
            allow users invest with crypto or local currency (fiat currency)
            without paying commission. You choose an investment plan suitable
            for you and you make your investment and let us handle your
            investment and ensure you receive your income regularly every month
            (income arrives every week).
          </Typography>
          <Typography as='p' className='text2'>
            We do the math for you; we use our powerful AI Arbitrage software
            for trading and managements of your assets, our AI develop
            mathematical algorithm designed to predict crypto environments and
            market behaviors to provide optimized liquidity.
          </Typography>
          <JoinButton variant='primary'>JOIN NOW</JoinButton>
        </StyledTextWrapper>
      </WorksContainer>
    </WorksSection>
  );
};

export default WorksSectionComponent;
