import React from 'react';
import { styled } from 'linaria/react';
import Image from '@components/atoms/image';

import RickImg from '@static/images/team/rick.jpg';
import Typography from '@components/atoms/typography';

const StyledTextWrapper = styled.div`
  padding: 17px 50px;
`;

const StyledTeamCardHeader = styled.div`
  height: 280px;
`;

const StyledTeamCardBody = styled.div`
  height: 110px;
  background-color: var(--accent-color);
`;

const StyledTeamCardContainer = styled.div`
  width: 270px;
  height: 400px;
  text-align: center;

  .title {
    font-size: 1.1rem;
    color: var(--primary-text-color);
    font-weight: bold;
  }

  .description {
    margin-top: 10px;
    line-height: 140%;
  }
`;

const TeamCard = ({ image, title, description }) => {
  return (
    <StyledTeamCardContainer>
      <StyledTeamCardHeader>
        <Image src={RickImg} alt='' />
      </StyledTeamCardHeader>
      <StyledTeamCardBody>
        <StyledTextWrapper>
          <Typography as='h3' className='subtitle2 title'>
            Rick Cole
          </Typography>
          <Typography as='p' className='text2 description'>
            Chief Operating Officer (COO)
          </Typography>
        </StyledTextWrapper>
      </StyledTeamCardBody>
    </StyledTeamCardContainer>
  );
};

export default TeamCard;
