import React from 'react';
import TradingWidget from './trading-widget.component';

const config = {
  symbols: [
    {
      title: 'S&P 500',
      proName: 'OANDA:SPX500USD',
    },
    {
      title: 'Nasdaq 100',
      proName: 'OANDA:NAS100USD',
    },
    {
      title: 'EUR/USD',
      proName: 'FX_IDC:EURUSD',
    },
    {
      title: 'BTC/USD',
      proName: 'BITSTAMP:BTCUSD',
    },
    {
      title: 'ETH/USD',
      proName: 'BITSTAMP:ETHUSD',
    },
  ],
  colorTheme: 'light',
  isTransparent: false,
  displayMode: 'adaptive',
  locale: 'en',
};

const src =
  'https://s3.tradingview.com/external-embedding/embed-widget-ticker-tape.js';

const TickerTaperWidget = (props) => {
  return <TradingWidget {...props} src={src} config={config} />;
};

export default TickerTaperWidget;
