import React from 'react';

import { styled } from 'linaria/react';

import Typography from '@components/atoms/typography';
import Button from '@components/atoms/button';
import QubeImage from '@components/molecules/qube-image';
import Container from '@components/atoms/container';
import Section from '@components/atoms/section';
import { media } from '@styles/media';

const StyledBannerInfo = styled.div`
  width: 100%;
  max-width: 670px;
  margin-top: -20px;
  margin: 0 auto;

  .title1 {
    margin: 10px 0;

    ${media.up('md')} {
      margin: 24px 0;
    }
  }

  .login-button {
    margin-top: 15px;

    ${media.up('md')} {
      margin-top: 50px;
    }
  }
`;

const BannerContainer = styled(Container)`
  .qube-image {
    padding: 0 30px;
  }

  ${media.up('xlg')} {
    flex-direction: row;

    .qube-image {
      order: 1;
      margin-left: 20px;
      padding: 0;
    }
  }
`;

const BannerSection = styled(Section)`
  background-image: var(--base-gradient-color);
  min-height: calc(100vh - 60px);
`;

const HeadBannerSection = () => {
  return (
    <BannerSection>
      <BannerContainer isFluid={true}>
        <QubeImage />
        <StyledBannerInfo>
          <Typography className='subtitle1'>World Link Coin</Typography>
          <Typography as='h1' className='title1'>
            The Most Trusted and Most Powerful <br /> Digital Asset Investment
            Platform!
          </Typography>
          <Typography as='p' className='text1'>
            A Global Leader in Digital Asset Management
          </Typography>
          <Button variant='primary' className='login-button'>
            Login
          </Button>
        </StyledBannerInfo>
      </BannerContainer>
    </BannerSection>
  );
};

export default HeadBannerSection;
