import React from 'react';

const Registration = () => {
  return (
    <div className='registration-wrapper'>
      <div className='registration-form'>
        <div className='title'>
          <h2>Registration</h2>
          <button className='close' type='button'></button>
        </div>
        <p>
          Already exist?
          <a className='sign-in-link' href='#'>
            Sign in
          </a>
        </p>
        <form method='post'>
          <input name='name' placeholder='Full Name*' required type='text' />
          <input name='email' placeholder='E-mail*' required type='text' />
          <input name='country' placeholder='Country*' required type='text' />
          <input name='city' placeholder='City*' required type='text' />
          <input name='tel' placeholder='Mobile*' required type='tel' />
          <input
            id='password'
            name='password'
            placeholder='Password*'
            required
            type='password'
          />
          <input
            id='password-check'
            name='password-check'
            placeholder='Confirm password*'
            required
            type='password'
          />
          <input
            className='button'
            style={{ width: '100%' }}
            type='submit'
            value='Register'
          />
        </form>
        <div className='footer-form'>
          <span>Sign Up from</span>
          <div>
            <a href='#'>Google</a>
            <a href='#'>Facebook</a>
          </div>
        </div>
      </div>

      <div className='sign-in-form'>
        <div className='title'>
          <h2>Sign In</h2>
          <button className='close' type='button'></button>
        </div>
        <p>
          No account?
          <a className='sign-up-link' href='#'>
            Sign Up
          </a>
        </p>
        <form method='post'>
          <input name='email' placeholder='E-mail*' required type='text' />
          <input
            name='password'
            placeholder='Password*'
            required
            type='password'
          />
          <label>
            <input name='remember' type='checkbox' />
            Remember me
          </label>
          <input
            className='button'
            style={{ width: '100%' }}
            type='submit'
            value='Login'
          />
        </form>
        <div className='footer-form sign-footer'>
          <span>Sign In from</span>
          <div>
            <a href='#'>Google</a>
            <a href='#'>Facebook</a>
          </div>
        </div>
        <div className='forgot-pass'>
          <a href='#'>Forgot password?</a>
        </div>
      </div>
    </div>
  );
};

export default Registration;
