import React from 'react';
import Image from '@components/atoms/image';
import { styled } from 'linaria/react';

const StyledQubeImageContainer = styled.div`
  max-width: 450px;
  /* width: 174px; */

  @media (min-width: 768px) {
    /* width: 450px; */
  }

  .shadow {
    animation-name: bounce-shadow;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  .qube {
    animation-name: bounce;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    z-index: 1;
  }

  @keyframes bounce-shadow {
    0% {
      transform: rotateY(0deg);
    }

    50% {
      transform: rotateY(60deg);
    }

    100% {
      transform: rotateY(0deg);
    }
  }

  @keyframes bounce {
    0% {
      transform: translateY(10px);
    }

    50% {
      transform: translateY(-20px);
    }

    100% {
      transform: translateY(10px);
    }
  }
`;

const QubeImage = () => {
  return (
    <StyledQubeImageContainer className='qube-image'>
      <Image alt='' className='qube' src='/images/qube.png' />
      <Image alt='' className='shadow' src='/images/shadow.png' />
    </StyledQubeImageContainer>
  );
};

export default QubeImage;
