import React from 'react';
import { styled } from 'linaria/react';

import TeamCard from './team-card.component';
import { media } from '@styles/media';

const StyledTeamList = styled.ul`
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 20px;

  ${media.up('md')} {
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
  }

  ${media.up('xlg')} {
    grid-template-columns: repeat(4, 1fr);
  }
`;

const StyledTeamListItem = styled.li``;

const TeamList = ({ className }) => {
  return (
    <StyledTeamList className={className}>
      <StyledTeamListItem>
        <TeamCard image='' title={''} description={''} />
      </StyledTeamListItem>
      <StyledTeamListItem>
        <TeamCard image='' title={''} description={''} />
      </StyledTeamListItem>
      <StyledTeamListItem>
        <TeamCard image='' title={''} description={''} />
      </StyledTeamListItem>
      <StyledTeamListItem>
        <TeamCard image='' title={''} description={''} />
      </StyledTeamListItem>
    </StyledTeamList>
  );
};

export default TeamList;
