import React from 'react';
import Typography from '@components/atoms/typography';
import Section from '@components/atoms/section';
import Container from '@components/atoms/container';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import { styled } from 'linaria/react';

const StyledTextWrapper = styled.div`
  color: var(--base-title-color);
  margin-top: 20px;

  display: flex;
  flex-direction: column;

  .text2 {
    color: var(--base-title-color);
  }

  .text2 + .text2 {
    margin-top: 20px;
  }

  ${media.up('md')} {
    margin-top: 40px;
  }

  ${media.up('xlg')} {
    flex-direction: row;

    .text2 + .text2 {
      margin-top: 0;
      margin-left: 100px;
    }
  }
`;

const ReadMoreButton = styled(Button)`
  margin-top: 30px;
  margin-right: auto;

  ${media.up('md')} {
    margin-top: 40px;
  }

  ${media.up('xlg')} {
    margin-top: 50px;
  }
`;

const DigitalPlatformContainer = styled(Container)`
  & > .title2 {
    color: var(--base-title-color);
    margin-top: 10px;
  }

  ${media.up('md')} {
    margin-top: 20px;
  }
`;

const DigitalPlatformSection = styled(Section)`
  padding-top: 0;
`;

const DigitalPlatformInformation = () => {
  return (
    <DigitalPlatformSection>
      <DigitalPlatformContainer isFluid>
        <Typography as='span' className='subtitle2'>
          What is world link coin?
        </Typography>
        <Typography as='h2' className='title2'>
          What is world link coin?
        </Typography>
        <StyledTextWrapper>
          <Typography as='p' className='text2'>
            Our WLC AI system is built to generate passive income with your
            investment and significantly multiply your income, we enable
            investors earn 5% - 25% monthly as (income arrives weekly), the
            minimum investment is $1000 USD.
          </Typography>
          <Typography as='p' className='text2'>
            Our WLC AI system is built to generate passive income with your
            investment and significantly multiply your income, we enable
            investors earn 5% - 25% monthly as (income arrives weekly), the
            minimum investment is $1000 USD.
          </Typography>
        </StyledTextWrapper>
        <ReadMoreButton variant='primary'>READ MORE</ReadMoreButton>
      </DigitalPlatformContainer>
    </DigitalPlatformSection>
  );
};

export default DigitalPlatformInformation;
