import React from 'react';
import Container from '@components/atoms/container';
import Section from '@components/atoms/section';
import { media } from '@styles/media';
import Button from '@components/atoms/button';
import Typography from '@components/atoms/typography';
import { styled } from 'linaria/react';

const Column = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

const Columns = styled.div`
  display: grid;
`;

const TextColumns = styled(Columns)`
  grid-template-columns: 1fr;
  grid-row-gap: 20px;

  ${media.up('xlg')} {
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 45px;
    grid-column-gap: 80px;
  }
`;

const StyledTextGroup = styled.div`
  .subtitle2 {
    text-transform: uppercase;
  }
  .text2 {
    margin-top: 10px;
  }
`;

const QuestionContainer = styled(Container)`
  align-items: flex-start;

  .text2,
  .title2 {
    color: var(--base-title-color);
  }

  .title2 {
    margin-bottom: 30px;
  }

  .start-button {
    margin-top: 30px;
  }
`;

const QuestionSection = styled(Section)``;

const QuestionSectionComponent = () => {
  return (
    <QuestionSection>
      <QuestionContainer isFluid={true}>
        <Typography as='h2' className='title2'>
          Why world link coin?
        </Typography>
        <TextColumns>
          {/* <LeftWrapper> */}
          <Column>
            <StyledTextGroup>
              <Typography as='span' className='subtitle2'>
                24/7/365 Days of Operation and Support
              </Typography>
              <Typography as='p' className='text2'>
                Our AI Arbitrage system is always working on auto-pilot (24/7)
                and crypto never sleeps, neither do we.
              </Typography>
            </StyledTextGroup>
          </Column>
          <Column>
            <StyledTextGroup>
              <Typography as='span' className='subtitle2'>
                No fees or Commission
              </Typography>
              <Typography as='p' className='text2'>
                We do not take commission to manage your investment and no extra
                fees like monthly management fee, transaction fees or membership
                fees.
              </Typography>
            </StyledTextGroup>
          </Column>
          {/* </LeftWrapper> */}
          {/* <RightWrapper> */}
          <Column>
            <StyledTextGroup>
              <Typography as='span' className='subtitle2'>
                Zero Risk
              </Typography>
              <Typography as='p' className='text2'>
                We can guarantee a risk free investment for our customers
                because we use the most powerful AI Arbitrage software for
                trading and managing of your crypto .
              </Typography>
            </StyledTextGroup>
          </Column>
          <Column>
            <StyledTextGroup>
              <Typography as='span' className='subtitle2'>
                Capital Protection
              </Typography>
              <Typography as='p' className='text2'>
                Your digital currency or investments are kept protected in our
                proprietary cold and secured storage vault.
              </Typography>
            </StyledTextGroup>
          </Column>
          <Column>
            <StyledTextGroup>
              <Typography as='span' className='subtitle2'>
                Capital Protection
              </Typography>
              <Typography as='p' className='text2'>
                Your digital currency or investments are kept protected in our
                proprietary cold and secured storage vault.
              </Typography>
            </StyledTextGroup>
          </Column>
          {/* </RightWrapper> */}
        </TextColumns>
        <Button variant='primary' className='start-button'>
          Let's start
        </Button>
      </QuestionContainer>
    </QuestionSection>
  );
};

export default QuestionSectionComponent;
