import React from 'react';
import Section from '@components/atoms/section';
import Typography from '@components/atoms/typography';
import Container from '@components/atoms/container';
import { styled } from 'linaria/react';
import Button from '@components/atoms/button';
import { cx } from 'linaria';
import { media } from '@styles/media';

const StyledChooseButton = styled(Button)`
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;

  width: 100%;
  padding: 16px 20px;

  transition-duration: 0.3s;
  transition-property: box-shadow, transform;

  text-transform: uppercase;

  &:hover {
    box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
    transform: scale(1.05);
  }

  &.is-active {
    background-color: var(--secondary-text-color);
  }

  & > .subtitle2 {
    color: var(--primary-text-color);
    text-transform: uppercase;
  }
`;

const StyledChooseListItem = styled.li`
  & + & {
    margin-top: 10px;
  }
`;

const StyledChooseList = styled.ul``;

const ChooseList = ({ className, data, activeItemId, onClick }) => {
  const list = data.map(({ title }, idx) => {
    const active = activeItemId === idx;

    const handleClick = () => {
      if (!active) {
        onClick(idx);
      }
    };

    return (
      <StyledChooseListItem key={idx}>
        <StyledChooseButton
          className={cx(active && 'is-active')}
          onClick={handleClick}
        >
          <Typography as='h3' className='subtitle2'>
            {title}
          </Typography>
        </StyledChooseButton>
      </StyledChooseListItem>
    );
  });

  return <StyledChooseList className={className}>{list}</StyledChooseList>;
};

const StyledChooseInfoContainer = styled.div`
  --head-height: 13px;

  &::before {
    content: '';
    display: block;
    height: var(--head-height);
    width: calc(100% - 20px);
    margin: 0 auto;

    border-radius: 8px 8px 0 0;

    background-color: var(--secondary-text-color);
    opacity: 0.3;
  }

  ${media.up('xlg')} {
    --head-height: 20px;
  }
`;

const StyledChooseInfo = styled.div`
  min-height: 200px;
  height: calc(100% - var(--head-height));

  padding: 24px 16px;

  overflow-y: auto;

  border-radius: 8px;

  background-color: var(--secondary-text-color);

  & > .subtitle2 {
    color: var(--primary-text-color);
    text-transform: uppercase;
  }

  & > .text2 {
    margin-top: 10px;
  }
`;

const ChooseInfo = ({ title, text }) => {
  return (
    <StyledChooseInfoContainer>
      <StyledChooseInfo>
        <Typography as='h4' className='subtitle2'>
          {title}
        </Typography>
        <Typography as='p' className='text2'>
          {text}
        </Typography>
      </StyledChooseInfo>
    </StyledChooseInfoContainer>
  );
};

const StyledChooseBlockContainer = styled.div`
  display: flex;
  flex-direction: column;

  .choose-list {
    margin-top: 10px;
  }

  ${media.up('xlg')} {
    flex-direction: row;
    align-items: stretch;

    & > * {
      flex: 1;
    }

    .choose-list {
      order: -1;
      margin-right: 20px;
      margin-top: 0;
    }
  }
`;

const ChooseBlock = ({ data }) => {
  const [activeItemId, setActiveItemId] = React.useState(0);
  const { title, text } = data[activeItemId];

  const handleClickItem = React.useCallback((id) => {
    setActiveItemId(id);
  }, []);

  return (
    <StyledChooseBlockContainer>
      <ChooseInfo title={title} text={text} />
      <ChooseList
        data={data}
        activeItemId={activeItemId}
        onClick={handleClickItem}
        className='choose-list'
      />
    </StyledChooseBlockContainer>
  );
};

const ChooseContainer = styled(Container)`
  & > .title2 {
    margin: 10px 0 40px;
  }
`;

const ChooseSection = styled(Section)`
  background-image: var(--base-gradient-color);
`;

const data = [
  {
    title: 'Super1',
    text:
      'Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta',
  },
  {
    title: 'Super2',
    text:
      'Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta',
  },
  {
    title: 'Super3',
    text:
      'Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta',
  },
  {
    title: 'Super4',
    text:
      'Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta',
  },
  {
    title: 'Super5',
    text:
      'Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta Super mnogo texta',
  },
];

const ChooseSectionComponent = () => {
  return (
    <ChooseSection variant='sm'>
      <ChooseContainer isFluid={true}>
        <Typography as='span' className='subtitle2'>
          THE FIRST AND MOST POWERFUL AI ARBITRAGE INVESTMENT PLATFORM
        </Typography>
        <Typography as='h2' className='title2'>
          Why Choose World Link Coin
        </Typography>
        <ChooseBlock data={data} />
      </ChooseContainer>
    </ChooseSection>
  );
};

export default ChooseSectionComponent;
