import React from 'react';
import { styled } from 'linaria/react';

//http://brenna.github.io/csshexagon/

const StyledHexagon = styled.div`
  position: relative;
  width: 40px;
  height: 23.09px;
  background-color: ${(p) => p.color};
  margin: 11.55px 0;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 0;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
  }

  &::before {
    bottom: 100%;
    border-bottom: 11.55px solid ${(p) => p.color};
  }

  &::after {
    top: 100%;
    width: 0;
    border-top: 11.55px solid ${(p) => p.color};
  }
`;

const Hexagon = (props) => {
  return <StyledHexagon {...props} />;
};

Hexagon.defaultProps = {
  color: 'inherit',
};

export default Hexagon;
